/** Объект с массивами индексов маски поля ввода */
interface DelimitersAndValuesIndexes {
  /** Индексы разделителей (пунктуационные, арифметические и прочие символы) */
  delimiterIndexes: number[];

  /** Индексы символов русских/английских букв и цифр */
  inputValueIndexes: number[];
}

/** Разбирает переданную маску посивмольно и сохраняет 2 массива с индексами буквенно-числовых и всех прочих символов
 * @param {string} mask маска поля ввода
 * @returns {DelimitersAndValuesIndexes} объект с массивами индексов маски поля ввода
 */
const getMaskSymbolsIndexes = (mask: string): DelimitersAndValuesIndexes => (
  mask.split('').reduce(
    (result: DelimitersAndValuesIndexes, letter: string, index: number) => (/[0-9A-ZА-Я]/gi.test(letter)
      ? ({ ...result, inputValueIndexes: [...result.inputValueIndexes, index] })
      : ({ ...result, delimiterIndexes: [...result.delimiterIndexes, index] })),
    { delimiterIndexes: [], inputValueIndexes: [] } as DelimitersAndValuesIndexes
  ));

export default getMaskSymbolsIndexes;
